    content3:
        'Innym sposobem jest umieszczenie farby na folii bbelkowej (jej wypukej czci), a nastpnie owinicie ni jajka. Gdy farba ju wyschnie, naley pomalowa j lakierem, aby utrwali efekt.',
    subheader4: '',
    content4:
        'Ogromny blok, usytuowany przy alei Wojciecha Korfantego, powsta wedug projektu Mieczysawa Krla. Przez kilka lat dziery palm pierwszestwa pod wzgldem liczby mieszkacw. Budynek cignie si na dugoci 187,5 metra. Ma 15 kondygnacji nadziemnych i ukryte w podziemiu garae na 200 samochodw. Posadowiony jest na elbetowych filarach, co ma zapobiec niebezpiecznym skutkom podmuchw wiatru i wstrzsw tektonicznych.',
